import React from 'react'
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import InstagramIcon from '@material-ui/icons/Instagram';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import PhoneIcon from '@material-ui/icons/Phone';
import { Grid } from '@material-ui/core';


function Footer() {
    return (
        <Grid container className="footeri" direction="column" >
            <Grid container alignContent="center" justify="center" className="contactinfo" direction="column">
                <Grid item xs={12}>
                    <h1 className="contactinfo2" >Contact</h1>
                </Grid>
                <Grid item xs={7}>
                    <p className="footertxt"> Interested in working together? <br/><a href="mailto:melhemdirani@hotmail.com" className="click"> click here </a></p>
                </Grid>
            </Grid >
            <Grid  container className="footer" id="contact" justify="center" alignContent="center">
                    <a className="contact" href="mailto:melhemdirani@hotmail.com" id="Contact" >
                    <MailOutlineIcon className="icon" />
                    </a>
                    <a className="contact"  title="Call SF" href="tel:+96171523153">
                        <PhoneIcon className="icon" />
                    </a>
                    <a className="contact" href="https://facebook.com/mel7em" target="_blank" title="Facebook">
                    <FacebookIcon className="icon" />
                    </a>
                    <a  className="contact"   href="https://github.com/melhemdirani" target="_blank" title="GitHub">
                    <GitHubIcon className="icon"/>
                    </a>
                    <a className="contact"  href="https://instagram.com/melhem_dirani" title="Instagram">
                    <InstagramIcon className="icon" />
                    </a>
            </Grid>
        </Grid>
    )
}

export default Footer
