import React from 'react';
import ProfilePic from './ProfilePic.png';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Grid, makeStyles } from '@material-ui/core';
import '../Hidden.css';

const useStyles = makeStyles((theme) => ({
    container: {
      display: 'grid',
      gridTemplateColumns: 'repeat(12, 1fr)',
      gridGap: theme.spacing(3),
    },
    paper: {
      padding: theme.spacing(1),
      textAlign: 'center',
      color: theme.palette.text.secondary,
      whiteSpace: 'nowrap',
      marginBottom: theme.spacing(1),
    },
    divider: {
      margin: theme.spacing(2, 0),
    },
  }));
function Header() {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const classes = useStyles();
    return (
    <div className="header">
        <div className="menu-hdn hidden2">
            <button className="button-hdn" onClick={handleClick}>
             <MenuIcon className="MenuIcon"/>
            </button>
            <Menu
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
            >
                <a className="ahvr-hdn" href="#projects" onClick={handleClose}>Projects</a>
                <a className="ahvr-hdn" href="#about" onClick={handleClose}>About me</a>
                <a className="ahvr-hdn" href="#contact" onClick={handleClose}>Contact</a>
            </Menu>
        </div>
        <Grid container className="header2" alignItems="flex-end" direction="column">
                <Grid item xs={12}  alignItems="flex-start" >
                <a className="ahvr" href="#projects">Projects</a>
                <a className="ahvr" href="#about">About me</a>
                <a className="ahvr" href="#contact">Contact</a>
                </Grid> 
                <Grid container  xs={12} direction="column" alignItems="center" className="Margin">
                    <Grid item xs={12} >
                    <h1 className="h1">Hello I'm Melhem El Dirani</h1>
                    </Grid>
                    <Grid item xs={12} >
                    <p className="p">And I build things for the web</p>
                    </Grid>
                    <Grid item xs={12} >
                    <a href="#about" className="button">Find Out More</a>
                    </Grid>
                </Grid>
        </Grid>
        <Grid container className="about" id="about" justify="space-evenly" alignItems="center">
            <Grid item md={3} xs={12}>
                <img className="profilepic" src={ProfilePic} alt="Melhem" />
            </Grid>
            <Grid item md={6} xs={12}  className="aboutme">  
                <h1 className="text1" >About me</h1>
                <p className="text2">I found my passion in web development in the middle of the 2020 pandemic, quitted my teaching job and dived in the IT field</p>  
                <p className="text2">Innovative Front End Developer with experience in building and maintaining responsive websites. Proficient in HTML, CSS, JavaScript and React; plus modern libraries and frameworks. </p>
            </Grid>
        </Grid>
        <Grid item xs={12} >
            <h1 className="title" id="projects"> Projects</h1>
        </Grid>
    </div>
    )
}

export default Header
