import { Grid } from '@material-ui/core'
import React, { useLayoutEffect, useState } from 'react';

function useWindowSize() {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth, window.innerHeight]);
      }
      window.addEventListener('resize', updateSize);
      updateSize();
      return () => window.removeEventListener('resize', updateSize);
    }, []);
    return size;
  }
 
function Projects({image,projectName,link,link2}) {
    const [width, height] = useWindowSize();
    
    return (
        
        <div>
            {width > 600 ?
                <Grid container className="project" direction="row-reverse" justify="center">
                            
                    <Grid item md={6} xs={12}>
                        <img className="projectimg rotate " src={image} alt=""/>
                    </Grid>
        
                    <Grid item className="desc" xs={3}>
                        <h1 className="pn">{projectName}</h1>
                        <div className="link">
                            <a className="link1" href={link} target="_blank"> View Live </a>
                            <a className="link1" href={link2} target="_blank"> Source Code </a>
                        </div>  
                    </Grid>
                </Grid>
        :
        <Grid container className="project" direction="row" alignContent="center" justify="center">
         
        <Grid item xs={6}>
            <h1 className="pn">{projectName}</h1>
        </Grid>                   
        <Grid item  xs={7} >
            <img className="projectimg" src={image} alt=""/>
        </Grid>

        <Grid item className="desc" xs={16}>
                <a className="link1" href={link} target="_blank"> View Live </a>
                <a className="link1" href={link2} target="_blank"> Source Code </a> 
        </Grid>
    </Grid>}
        
        </div>
    )

} 

export default Projects
